import loginSheet from "../../components/loginSheet";
export default {
  data() {
    return {};
  },
  components: {
    loginSheet
  },
  methods: {
    onGetAuthorize() {
      uni.login({
        success: res => {
          var code = res.code;
          console.log(code);
          uni.getUserInfo({
            success: res => {
              console.log(res);
            }
          });
        },
        fail(e) {
          console.log(e);
        }
      });
    }
  }
};
